@import url(https://fonts.googleapis.com/css?family=Lobster+Two&display=swap);
.card-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.card {
    width: 250px;
    height: 450px;
    border: 5px solid rgb(0, 214, 0);
    border-radius: 10px;
    display: grid;
    grid-template-rows: 1fr 2fr 3fr;
    background-color: rgb(0, 53, 0);
    color: #fff;
}

.name,
.description {
    padding: 1rem;
}

.name {
    text-align: center;
}

.picture {
    width: 100%;
    height: 100%;
    background-color: white;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}

.description {
    font-size: 0.9rem;
}

button {
    width: 250px;
    border: 2px solid rgb(112, 112, 112);
    font-size: 1.2rem;
    padding: 1.2rem;
    border-radius: 10px;
    margin: 1rem 0;
    cursor: pointer;
    font-weight: 600;
}
h1 {
    font-size: 4rem;
    text-align: center;
    margin: 2rem 0;
    font-family: 'Lobster Two', sans-serif;
}

.container {
    width: 90%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 2rem 1rem;
    padding: 2rem;
}

@media screen and (max-width: 400px){
    .container {
        padding: 0;
        grid-gap: 1rem;
        width: 100%;
        margin-top: 3rem
    }   
}
.new-card {
    width: 250px;
    height: auto;
    background-color: rgb(0, 53, 0);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    padding: 1rem;
    box-shadow: 1px 1px 8px rgb(155, 155, 155);
    margin: 1rem auto;
    border-radius: 10px;
    border: 5px solid rgb(0, 214, 0);
    color: #fff;
}

h3 {
    margin-bottom: 1rem;
}

label {
    width: 100%;
    text-align: center;
    font-weight: 600;
}

input,
textarea {
    width: 100%;
    padding: 0.5rem;    
    border: 1px solid rgb(155, 155, 155);
    border-radius: 10px;
    margin-top: 0.5rem;
}

.button {
    width: 100%;
    background-color: rgb(0, 214, 0);
    margin-top: 1rem;
    font-size: 1.1rem;
    font-weight: 600;
    color: rgb(0, 53, 0);
    cursor: pointer;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background: #A1FFCE;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-gradient(linear, right bottom, left top, from(#FAFFD1), to(#A1FFCE));
    background: linear-gradient(to left top, #FAFFD1, #A1FFCE); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
